import * as React from 'react';
import clsx from 'clsx';
import { withStyles, WithStyles, ButtonBase } from '@material-ui/core';
import scrollIntoView from 'scroll-into-view';
import Loadable from '@loadable/component';
import { Link as RouterLink } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
import get from 'lodash/get';
import { withRouter, RouteComponentProps } from 'react-router';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import Link from '@material-ui/core/Link';
import PictureAsPdfIcon from '@material-ui/icons/PictureAsPdf';
import { RootState } from '../../reducers';
import {
	ChangeSectionAction,
	Content,
	GetInvestorRelationsContentAction,
	InvestorContent,
	Selections,
	SelectedCategoryAction
} from '../../redux/Investors/types';
import Style from './style';
import { Resources } from '../../constants/resources';

const BreadcrumbSeparator = Loadable(() => import('../../components/BreadcrumbSeparator'));

interface Props extends WithStyles<typeof Style>, RouteComponentProps {
	selections: Selections;
	getInvestorRelationsContent: typeof GetInvestorRelationsContentAction.action;
	changeSection: typeof ChangeSectionAction.action;
	selectedCategoryAction: typeof SelectedCategoryAction.action;
}
/**
 * InvestorRelationsSubMenu page.
 * @param {any} props - Properties
 * @returns {React.Component} component.
 */
class InvestorRelationsSubMenu extends React.PureComponent<Props> {
	componentDidMount() {
		const {
			selections,
			getInvestorRelationsContent,
			match,
			changeSection,
			selectedCategoryAction,
			location
		} = this.props;
		const selectedSection = get(selections, 'section', '');
		if (selectedSection === '') {
			getInvestorRelationsContent();
		}
		const { search } = location;
		const url = new URLSearchParams(search);
		const section = url.get('section') || '';
		const menu = url.get('menu') || '';
		changeSection({ section, menu });
		// scrollIntoView(document.getElementById('home') as any);
	}

	/**
	 * Render method
	 * @returns {JSX.Element} Jsx.
	 */
	public render() {
		const { classes, selections, match, location } = this.props;
		const subcategories: Array<string> = get(selections, 'subcategories', []);
		const showFiles: Array<any> = get(selections, 'showFiles', []);
		const { search } = location;
		const url = new URLSearchParams(search);
		const section = url.get('section') || '';
		const menu = url.get('menu') || '';
		const {
			REACT_APP_API_URL = '',
			REACT_APP_CONTENT_BASE_URL = '',
			REACT_APP_FILE_API = ''
		} = process.env;
		const CONTENT_FILE_API =
			REACT_APP_API_URL + REACT_APP_CONTENT_BASE_URL + REACT_APP_FILE_API;
		return (
			<div className={classes.block}>
				<div className={classes.largerFont}>{Resources.InvestorRelationsTitle}</div>
				<Breadcrumbs
					separator={<BreadcrumbSeparator />}
					aria-label="breadcrumb"
					classes={{ separator: classes.separator }}
				>
					<Link
						component={RouterLink as any}
						to="/investor-relationship"
						className={classes.activeLink}
					>
						{section}
					</Link>
					<span className={classes.smallFont}>{menu}</span>
				</Breadcrumbs>
				<Grid container spacing={1}>
					{subcategories.map((category) => (
						<Grid item xs={12} sm={12} key={category}>
							<Link
								component={RouterLink as any}
								to={`/investor-relationship?section=${section}&menu=${menu}&category=${category}`}
								className={clsx(classes.link)}
								key={category}
							>
								<ButtonBase className={classes.button} disableRipple>
									{category}
								</ButtonBase>
							</Link>
						</Grid>
					))}
					{showFiles.map(({ files, heading }) => (
						<>
							<Grid
								item
								xs={12}
								sm={12}
								className={clsx(classes.fontSize_18, classes.marginTop_16)}
							>
								{heading}
							</Grid>
							{files.map((file) => (
								<Grid item xs={12} sm={12} key={file.fileId}>
									<div className={classes.linkBody}>
										<PictureAsPdfIcon className={classes.largeFont} />
										<Link
											className={clsx(classes.link)}
											href={`${CONTENT_FILE_API}/${file.fileId}/${file.fileName}`}
											color="secondary"
											target="__blank__"
										>
											{file.fileName.replace(/(\.pdf)/gi, '')}
										</Link>
									</div>
								</Grid>
							))}
						</>
					))}
				</Grid>
			</div>
		);
	}
}

const mapStateToProps = (state: RootState) => ({
	selections: state.investorRelations.selections
});

const mapDispatchToProps = {
	getInvestorRelationsContent: GetInvestorRelationsContentAction.action,
	changeSection: ChangeSectionAction.action,
	selectedCategoryAction: SelectedCategoryAction.action
};

export default withStyles(Style, { name: 'DmartCorpFreshInvestorRelationsSubMenu' })(
	withRouter(connect(mapStateToProps, mapDispatchToProps)(InvestorRelationsSubMenu))
);
